/**
 *
 * DatasourceSelect
 *
 */

import React from 'react';
// import styled from 'styled-components';

import { injectIntl, FormattedMessage } from 'react-intl';
import messages from './messages';
import { CBEvent, SettingTypes, SettingOption } from 'utils/types';
import CbInputField from 'components/CbInputField';
import Port from 'cb-utils/models/PortalModel';
// @ts-ignore - type defs for this lib aren't accurate
import { Menu, MenuItem, Highlighter } from 'react-bootstrap-typeahead';
import groupBy from 'lodash/groupBy';
import map from 'lodash/map';

const portalModel = Port.getInstance();
const preferredDatasourceKey = 'true';

type DatasourceOption = SettingOption & { preferred: boolean };

export interface DatasourceSelectProps {
  datasources: DatasourceOption[];
  value: string;
  onChange: (e: CBEvent) => void;
  labelText: string;
  groupDatasources?: boolean;
  size?: number;
  inlineSize?: string;
}

type MasterProps = DatasourceSelectProps & ReactIntl.InjectedIntlProps;

class DatasourceSelect extends React.PureComponent<MasterProps, {}> {
  renderProps = {};

  constructor(props: MasterProps) {
    super(props);

    if (props.groupDatasources) {
      this.renderProps = {
        renderMenu: this.renderMenu,
      };
    }
  }

  render() {
    const {
      datasources,
      value,
      onChange,
      intl: { formatMessage },
      labelText,
      size,
      inlineSize,
    } = this.props;
    return (
      <CbInputField
        size={size}
        inlineSize={inlineSize}
        type={SettingTypes.TYPE_AHEAD_TYPE}
        placeholder={formatMessage(messages.selectDatasource)}
        labelText={labelText}
        name=""
        display_name=""
        {...this.getDatasourceError()}
        {...this.renderProps}
        value={value}
        onChange={(e: CBEvent) => {
          const val = e.target.value;
          if (val && portalModel.hasDatasourceWithId(val)) {
            onChange(e);
          }
        }}
        // @ts-ignore - we don't have a way to send field-specific props without explicitly listing them in CbInputField
        allowNew={false}
        options={datasources}
      />
    );
  }

  getDatasourceError = () => {
    const id = this.props.value;
    if (!portalModel.hasDatasourceWithId(id)) {
      return {
        showError: true,
        error: this.props.intl.formatMessage(messages.badDsId, { id }),
      };
    }
    return {};
  };

  renderMenu = (
    results: DatasourceSelectProps['datasources'],
    menuProps: { text: string },
  ) => {
    let idx = 0;
    const grouped = groupBy(results, (r) => r.preferred);
    const items = Object.keys(grouped)
      .sort((a, b) => {
        // show preferred datasources first
        if (a === preferredDatasourceKey) {
          return -1;
        } else if (b === preferredDatasourceKey) {
          return 1;
        }
        return 0;
      })
      .map((preferred) => {
        return [
          !!idx && <Menu.Divider key={`${preferred}-divider`} />,
          <Menu.Header key={`${preferred}-header`}>
            {preferred === preferredDatasourceKey ? (
              <FormattedMessage {...messages.preferredDatasources} />
            ) : (
              <FormattedMessage {...messages.otherDatasources} />
            )}
          </Menu.Header>,
          map(grouped[preferred], (state) => {
            const item = (
              <MenuItem key={idx} option={state} position={idx}>
                <Highlighter search={menuProps.text}>{state.name}</Highlighter>
              </MenuItem>
            );

            idx += 1;
            return item;
          }),
        ];
      });

    return <Menu {...menuProps}>{items}</Menu>;
  };
}

export default injectIntl(DatasourceSelect);



