const formTypeString = `type ColumnType =
  | 'string'
  | 'int'
  | 'bool'
  | 'timestamp'
  | 'float'
  | 'bigint'
  | 'double'
  | 'blob'
  | 'uuid';
interface FieldSetting {
  name: string;
  dataType: ColumnType;
  inputType?:
    | 'text'
    | 'color'
    | 'option'
    | 'boolean'
    | 'DATE_PICKER'
    | 'typeAhead'
    | 'MULTI_TYPE_AHEAD_TYPE'
    | 'readOnly'
    | 'copy'
    | 'password'
    | 'Slider'
    | 'RADIAL_GAUGE'
    | 'LARGE_NUMBER'
    | 'CHECK_OR_EX'
    | 'TOGGLE'
    | 'HTML'
    | 'BUTTON_GROUP';
  orderIdx?: number;
  label?: string;
  description?: string;
  smallText?: string;
  widthPercent?: string;
  display?: boolean;
  isKeyColumn?: boolean;
  isReadOnly?: boolean;
  key: string;
  dropdownOptions?: string[] | Array<{ name: string; value: string }>;
  validationType?: string;
  regexValidation?: string;
  errorText?: string;
  sliderMin?: string;
  sliderMax?: string;
  startDate?: string;
  endDate?: string;
  units?: string;
  progressColor?: string;
  allowNew?: boolean;
  defaultValue?: string;
  required?: boolean;
  placeholder?: string;
  parser?: { HTML: string; JavaScript: string; CSS: string };
}

interface FieldSettingsMap {
  [k: string]: FieldSetting;
}

interface FormData {
  [k: string]: any;
}

interface ColumnModel {
  ColumnName: string;
  ColumnType: ColumnType;
  PK: boolean;
}

interface FormSourceObj {
  data: FormData;
  onUpdate: (
    arg: {
      currentValues: FormData;
      fieldSettings: FieldSettingsMap;
    },
  ) => { currentValues: FormData; fieldSettings: FieldSettingsMap };
  overrideFieldSettings: FieldSettingsMap;
  schema: ColumnModel[];
}
`;

export default formTypeString;



